import React, { Component } from 'react';

import { Card, Row, Table, Col, Select, Input } from 'antd';

import Page from 'components/LayoutComponents/Page';
import { CartHeadAction } from 'components/common';
import { arrangeGV } from 'dump/data';
import './style.scss';

import SearchBarTrainingPlan from './FormTrainingPlan';
import MenuTabTop from './MenuTabTop';

const { Option } = Select;
class TrainingPlanTable extends Component {
  state = {};

  render() {
    const columns = [
      {
        title: 'Tên lớp',
        dataIndex: 'className',
      },
      {
        title: 'Tên môn',
        dataIndex: 'name',
      },
      {
        title: 'Số học trình',
        dataIndex: 'SHT',
      },
      {
        title: 'Lớp ghép',
        dataIndex: 'classG',
      },
      {
        title: 'Loại giờ',
        dataIndex: 'LG',
      },
      {
        title: 'Tốp thứ',
        dataIndex: 'TT',
      },
      {
        title: 'S tiết CTĐT',
        dataIndex: 'ST',
      },
      {
        title: 'S tiết tuần',
        // dataIndex: 'STT',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.STT} />,
      },
      {
        title: 'Từ tiết mấy',
        // dataIndex: 'TTM',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.TTM} />,
      },
      {
        title: 'Đến tiết mấy',
        // dataIndex: 'DTM',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.DTM} />,
      },
      {
        title: 'S tiết xếp KH',
        dataIndex: 'STXKH',
      },
      {
        title: 'S tiết chưa xếp KH',
        dataIndex: 'STCX',
      },
      {
        title: 'S tiết xếp TKB',
        dataIndex: 'STXTKB',
      },
      {
        title: 'Ca học',
        dataIndex: 'CH',
      },
      {
        title: 'Giai đoạn',
        dataIndex: 'GD',
      },
      {
        title: 'Từ tuần',
        dataIndex: 'TTT',
      },
      {
        title: 'Đến tuần',
        dataIndex: 'DT',
      },
      {
        title: 'Từ ngày',
        dataIndex: 'fromdate',
      },
      {
        title: 'Đến ngày ',
        dataIndex: 'todate',
      },
      {
        title: 'Ngày thi ',
        dataIndex: 'NT',
        width: '150px',
        render: (text, record) => (
          <Select style={{ width: '95%' }} defaultValue={record.NT}>
            <Option value="1">10/10/2015</Option>
            <Option value="2">12/10/2015 </Option>
            <Option value="3">1/10/2015 </Option>
          </Select>
        ),
      },
      {
        title: 'Ca thi ',
        dataIndex: 'CT',
        width: '150px',
        render: (text, record) => (
          <Select style={{ width: '95%' }} defaultValue={record.CT}>
            <Option value="1">4/10/2015 </Option>
            <Option value="2">5/10/2019 </Option>
            <Option value="3">10/1/2018 </Option>
          </Select>
        ),
      },
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
    };
    return (
      <Card title="Phân bổ giáo viên, ca học, phòng học">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <MenuTabTop />
            </CartHeadAction>
          </Col>
          <Col span={24}>
            <SearchBarTrainingPlan />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: 2000 }}
              rowSelection={rowSelection}
              bordered
              dataSource={arrangeGV}
              columns={columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingPlanTable />
  </Page>
);



